@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@200&family=Lato:wght@300&display=swap);
body {
  text-align: center;
  background-color: rgb(253, 253, 253);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
}

#root {
  height: 1000px;
  margin-top: -25px;
}
header {
  display: flex;
  flex-wrap: wrap;
  height: 80px;
  justify-content: space-around;
  margin: 25px 0 80px;
}

#name {
  align-items: center;
  background-color: rgb(0, 15, 0);
  color: white;
  display: flex;
  height: 60px;
  justify-content: space-between;
  padding-left: 35px;
  padding-right: 35px;
  width: 100%;
}

#pages {
  align-items: center;
  background-color: rgb(207, 207, 207);
  display: flex;
  height: 45px;
  justify-content: space-around;
  width: 100%;
}

#name-box {
  align-items: center;
  background-color: white;
  border-radius: 15px;
  color: black;
  display: flex;
  height: 50%;
  justify-content: center;
  min-width: 120px;
}

p {
  font-size: 15px;
  padding: 0 10px;
}

a {
  align-items: center;
  border-right: 1px solid white;
  color: rgb(11, 107, 11);
  display: flex;
  flex-grow: 1;
  font-size: 20px;
  font-weight: bold;
  height: 100%;
  justify-content: center;
  text-decoration: none;
  width: 33%;
}

a:hover {
  background-color: rgb(11, 107, 11);
  color: white;
}

img {
  width: 40px;
}

#header-image {
  align-items: center;
  display: flex;
}

#header-image h4 {
  font-size: 20px;
  padding-left: 20px;
}

#home-link {
  border: none;
  color: white;
}

#home-link:hover {
  background-color: black;
}

#page-album {
  display: flex;
  justify-content: space-around;
  width: 70%;
  margin: 0 auto;
}

#div1 {
  border-radius: 10px;
  margin-top: 150px;
  padding-top: 100px;
  transition: all 0.5s;
  width: 35%;
  height: 300px;
}

#div1 img {
  width: 60%;
}

#div1:hover {
  transition: 0.5s;
  width: 65%;
  height: 350px;
}

#div2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 65%;
}

h2 {
  color: rgb(51, 180, 51);
  margin-top: 300px;
}

#musicCard {
  align-items: center;
  border-radius: 10px;
  color: rgb(6, 61, 6);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 10px 0;
  padding: 10px 0;
  transition: all 0.5s;
  width: 80%;
}

#musicCard:hover {
  background-color: rgb(43, 151, 43);
  color: white;
  transition: 0.3s;
  width: 90%;
}

#musicCard h4 {
  font-size: 18px;
  text-align: center;
  width: 25%;
}

.checkbox {
  opacity: 0;
}

#coração {
  height: 25px;
  width: 50px;
}


#coração-vermelho {
  height: 25px;
  width: 50px;
}

.cards-label {
  align-items: center;
  display: flex;
  justify-content: center;
}

* {
  margin: 0;
}

#page-favorites {
  margin: 0 auto;
  width: 50%;
}

#musicCard {
  margin: 15px auto;
  width: 80%;
}

#remove-favorites-button {
  background-color: rgb(255, 124, 124);
  border: none;
  border-radius: 5px;
  color: white;
  box-shadow: 1px 5px red;
  font-weight: bold;
  font-size: 15px;
  height: 30px;
  margin-bottom: 20px;
  width: 150px;
}
.login-container {
  color: rgb(32, 32, 32);
  font-family: Verdana , Geneva , Tahoma , sans-serif;
  height: 300px;
  margin: 0 auto;
  text-align: center;
  width: 60%;
}

.login-input-button {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px rgb(105, 172, 4);
  display: flex;
  flex-wrap: wrap;
  height: 150px;
  justify-content: center;
  margin-top: 50px;
}

.login-input-button button {
  height: 25px;
  margin: 0 auto;
  transition: all 0.5s;
  width: 80%;
}

.login-input-button button:enabled {
  background-color: rgb(105, 172, 4);
  border: none;
  color: white;
  content: '>>';
  font-size: 15px;
  font-weight: bold;
  padding-right: 30px;
  transition: 0.5s;
}

.login-input-button button:enabled:after {
  content: ' >>';
  transition: 0.5s;
}

.login-image {
  margin-top: 25px;
}

.login-image img {
  width: 100px;
}

h1 {
  padding-top: 60px;
}

#login-input {
  border: solid 1px rgb(202, 202, 202);
  border-radius: 3px;
  font-size: 15px;
  height: 25px;
  margin: 0 auto;
  margin-top: 30px;
  width: 80%;
}

#login-input:hover {
  border: solid 2px rgb(165, 164, 164);
}

#loading-h2 {
  margin: 0;
  padding: 300px;
  color: rgb(10, 177, 10);
}

#page-profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  width: 80%;
}

#profile-section {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  max-width: 30%;
}

.profile-items {
  align-items: center;
  display: flex;
  justify-content: flex-start;
  margin: 20px auto;
  width: 100%;
}

.profile-items p {
  font-size: 17px;
  padding: 0 0 2px 10px;
}

#profile-img {
  height: 57px;
  margin-bottom: 50px;
  width: 57px;
}

#profile-button {
  border-radius: 5px;
  box-shadow: 0.5px 5px rgb(0, 47, 255);
  margin-top: 20px;
  width: 80px;
}

#profile-button:enabled {
  background-color: rgb(0, 140, 255);
  border: none;
  color: white;
  cursor: pointer;
  font-size: 15px;
  font-weight: bold;
  height: 35px;
  width: 110px;
}

#profile-link:hover {
  background-color: white;
}

#email {
  padding-bottom: 3px;
}
#profile-edit-form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 20%;
}

#link-div {
  display: flex;
  justify-content: center;
}

#link-div a:hover {
  background-color: white;
}

#link-div a button {
  transition: all 0.5s;
  width: 80px;
}

#link-div a button:enabled {
  background-color: rgb(0, 140, 255);
  border-radius: 5px;
  border: none;
  box-shadow: 0.5px 5px rgb(0, 47, 255);
  color: white;
  cursor: pointer;
  font-size: 15px;
  font-weight: bold;
  height: 35px;
  transition: 0.5s;
  width: 110px;
}

#inputs-div {
  display: flex;
  flex-direction: column;
  margin: 15px 0 0;
}

.labels {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

input {
  border: none;
  border-bottom: 1px solid black;
  font-size: 15px;
  margin: 25px 0 50px;
  width: 100%;
}

input:focus {
  background-color: rgb(246, 246, 246);
}

#image-div {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
.search-div {
  align-items: center;
  display: flex;
  justify-content: center;
}

.artist-input {
  height: 25px;
  margin-right: 10px;
  width: 250px;
}

.artist-button {
  margin: 0 0 27.2px 10px;
  transition: 0.5s;
}

.artist-button:enabled {
  background-color: rgb(2, 202, 35);
  border: none;
  box-shadow: 1px 5px rgb(0, 143, 0);
  color: white;
  font-size: 14px;
  font-weight: bold;
  height: 27px;
  width: 85px;
  border-radius: 3px;
}

.albums-div {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin: 0 auto;
  justify-content: center;
}

.album {
  align-items: center;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  margin: 10px;
  transition: all 0.5s;
  width: 20%;
  height: 300px;
}

.album img {
  max-height: 80%;
  padding-top: 10px;;
  width: 60%;
}

.album:hover {
  background-color: rgb(134, 238, 134);
  color: white;
  height: 320px;
  transition: 0.5s;
}

.link {
  border: none;
  color: black;
  font-size: 16px;
  transition: all 0.5s;
  width: 100%;
}

.link:hover {
  background-color: rgb(5, 199, 5);
  transition: 0.2s;
}

#loading {
  align-items: center;
  color: rgb(10, 177, 10);
  display: flex;
  height: 500px;
  justify-content: center;
  width: 100%;
}

#results {
  font-family: verdana;
  font-size: 27px;
  height: 20px;
  margin: 70px auto 60px;
  padding: 7px 0 0 180px;
  text-align: start;
  width: 80%;
}

#artistName {
  font-size: 15px;
  padding: 10px 0;
}

#App-div {
  background-color: rgb(255, 255, 255);
  height: 100%;
}
